import React, { useEffect, useMemo, useState } from 'react'
import { styled } from '@mui/material/styles'
import Card from 'components/Card'
import { TokenUtils, toLargeUnits } from 'utils/transformHelper'
import { useComputedIndexAction, usePositionInfo } from 'hooks/news/usePoistion'
import { useShortPendingAction, useShortInfo, useShortActiveAction } from 'state/Short/hooks'
import { TokenType } from 'state/Short/shortSlice'
import LinearIndeterminate from './CustomizedSlider'

const TopCard = styled(Card)`
  margin-bottom: 20px;
  display: flex;
  height: 175px;
  width: 100%;
  box-shadow: 0 4px 27px rgba(0, 0, 0, 0.03);
  backdrop-filter: unset;
`
const CtOne = styled('div')`
  width: 30%;
`
const CtTwo = styled('div')`
  width: 70%;
`
const CtLabel = styled('div')`
  color: rgba(0, 0, 0, 0.5);
`
const CtLabeltwo = styled('div')`
  margin-top: 10px;
  font-size: 12px;
  color: rgba(0, 0, 0, 0.5);
`
const CtValue = styled('div')`
  margin-bottom: 14px;
  font-family: PingFangSC-Semibold, sans-serif;
  font-weight: 550;
  font-size: 35px;
  margin-top: 24px;
  line-height: 35px;
  color: #000000;
`
const PtCt = styled('div')`
  display: flex;
`
const PtLabel = styled('div')`
  width: 25%;
`
const PtValue = styled('div')`
  width: 75%;
`
function DashboardContent() {
  const [linevalue, setLine] = useState({
    marks1: [
      {
        value: 0,
        label: '',
      },
      {
        value: 110,
        label: 'Liquidation',
      },
      {
        value: 160,
        label: 'Min. Mint',
      },
      {
        value: 300,
        label: '',
      },
    ],
    marks: [
      {
        value: 0,
        label: '',
      },
      {
        value: 110,
        label: '110%',
      },
      {
        value: 160,
        label: '120%',
      },
      {
        value: 300,
        label: '',
      },
    ],
  })
  const [linevaluep, setLinep] = useState({
    marks1: [],
    marks: [
      {
        value: 0,
        label: '',
      },
      {
        value: 110,
        label: '10',
      },

      {
        value: 160,
        label: '20',
      },
      {
        value: 300,
        label: '',
      },
    ],
  })
  const { currentPosition, currentPositionTokenInfo } = usePositionInfo()
  const { newCollateralRatio, newPrepaidInterestCoverage, onUpdateNeutralizeValue } = useComputedIndexAction()
  const { shortAmount, neutralizeAmount } = useShortPendingAction()
  const { activeInfo } = useShortActiveAction()
  const { rateInfo } = useShortInfo()
  const [load, setLoad] = useState(false)
  const { activeTab } = useShortInfo()
  useEffect(() => {
    let { coverage, rate } = rateInfo
    if (activeTab === 1) {
      if (shortAmount) {
        if (rateInfo.rate < 120) {
          coverage = 'N/A'
        } else if (rateInfo.rate >= 120 && coverage < 20) {
          coverage = 20
        }
      }
    } else if (activeTab === 2) {
      coverage = TokenUtils.formatTokenAmount(newPrepaidInterestCoverage, 0)
      rate = TokenUtils.formatTokenAmount(newCollateralRatio, 2)
    }

    setLinep((value) => {
      return {
        ...value,
        defaultvalue: coverage,
        showValue: coverage,
      }
    })
    setLine((value) => ({
      ...value,
      defaultvalue: rate,
      showValue: rate,
    }))
    setLoad(true)
  }, [activeTab, newCollateralRatio, newPrepaidInterestCoverage, rateInfo, shortAmount])

  const markPrice = useMemo(() => {
    if (currentPositionTokenInfo.tokenPrice) {
      return toLargeUnits(
        currentPositionTokenInfo.tokenPrice[currentPosition.mintTokenInfo.symbol]?.price,
        currentPosition.mintTokenInfo.settlementCurrency.decimals,
      )
    }
    return 0
  }, [currentPosition, currentPositionTokenInfo])

  useEffect(() => {
    if (activeInfo?.tokenType === TokenType.NEUTRALIZE) {
      const val = Number(activeInfo?.amount)
      onUpdateNeutralizeValue(val ? Number(`-${val}`) : 0)
    } else if (neutralizeAmount && activeInfo?.tokenType === TokenType.NEUTRALIZE) {
      const val = Number(neutralizeAmount)
      onUpdateNeutralizeValue(val ? Number(`-${val}`) : 0)
    }
  }, [activeInfo, onUpdateNeutralizeValue, neutralizeAmount, activeTab])

  return (
    <>
      <TopCard>
        <CtOne>
          <CtLabel>Outstanding Debt</CtLabel>
          <CtValue>
            {TokenUtils.formatFourDecimals(
              Number(toLargeUnits(currentPosition.balance, currentPosition.mintTokenInfo.decimals)),
            )}
          </CtValue>
          <CtLabeltwo>Mark Price: {TokenUtils.formatFourDecimals(Number(markPrice))} {currentPosition.mintTokenInfo.settlementCurrency.symbol}</CtLabeltwo>
        </CtOne>
        {load && (
          <CtTwo>
            <PtCt>
              <PtLabel style={{ marginTop: '45px' }}>Collateral Ratio</PtLabel>
              <PtValue>
                <LinearIndeterminate key={1} value={linevalue} />
              </PtValue>
            </PtCt>
            <PtCt>
              <PtLabel>Prepaid Interest Coverage</PtLabel>
              <PtValue>
                <LinearIndeterminate key={2} value={linevaluep} />
              </PtValue>
            </PtCt>
          </CtTwo>
        )}
      </TopCard>
    </>
  )
}

export default DashboardContent
