import { Box, Typography, TextField, InputAdornment } from '@mui/material'
import React, { useState } from 'react'
import Menu from '@mui/material/Menu'

const TradeSettings: React.FC<{
  handleClose: () => void
  anchorEl: null | HTMLElement
  open: boolean
  deadline: any
  maxSlippageValue: any

  setMaxSlippageValue: (value: any) => void
  setDeadlineValue: (value: string) => void
}> = ({ handleClose, anchorEl, open, deadline, maxSlippageValue, setMaxSlippageValue, setDeadlineValue }) => {
  const [focusFlag, setFocusFlag] = useState(false)
  const [chageFlag, setChangeFlag] = useState(false)
  const [chageDedFlag, setChangeDedFlag] = useState(false)

  return (
    <Menu
      id="basic-menu"
      anchorEl={anchorEl}
      open={open}
      onClose={handleClose}
      MenuListProps={{
        'aria-labelledby': 'basic-button',
      }}
      transformOrigin={{ horizontal: 'right', vertical: 'top' }}
      anchorOrigin={{ horizontal: 'right', vertical: 'bottom' }}
      PaperProps={{
        sx: {
          width: '288px',
          boxShadow: 'none',
          mt: 1.5,
          borderRadius: 2,
          border: '1px solid rgba(207, 216, 226, 0.5)',
        },
      }}
    >
      <Box
        sx={{
          pl: 1.2,
          pr: 1.2,
          mb: 1,
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
        }}
      >
        <Typography sx={{ fontSize: 14, fontWeight: 400, color: 'rgba(0, 0, 0, 0.5)' }}>Max.slippage</Typography>
        <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
          {/* <Typography sx={{ fontSize: 14, fontWeight: 500 }}>Auto</Typography> */}
          {/* <KeyboardArrowUpIcon sx={{ width: 16, height: 16 }} /> */}
        </Box>
      </Box>
      <Box
        sx={{
          display: 'flex',
          gap: 1,
          pl: 1.2,
          pr: 1.2,
          height: 50,
          width: 268,
          alignItems: 'center',
          ml: '9.6px',
          border: '1px solid #CFD8E2',
          borderRadius: 2.6,
        }}
      >
        <Box
          onClick={() => {
            setMaxSlippageValue('0.1')
            setChangeFlag(true)
          }}
          sx={{
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            borderRadius: 2.5,
            background: maxSlippageValue === '0.1' ? 'linear-gradient(270deg, #7AE8F1 0%, #29A0FF 100%)' : '',
            cursor: 'pointer',
            height: '42px',
            width: '42px',
            fontSize: '12px',
          }}
        >
          0.1%
        </Box>
        <Box
          onClick={() => {
            setMaxSlippageValue('0.5')
            setChangeFlag(true)
          }}
          sx={{
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            background: maxSlippageValue === '0.5' ? 'linear-gradient(270deg, #7AE8F1 0%, #29A0FF 100%)' : '',
            cursor: 'pointer',
            borderRadius: 2.5,
            height: '42px',
            width: '42px',
            fontSize: '12px',
          }}
        >
          0.5%
        </Box>
        <Box
          onClick={() => {
            setMaxSlippageValue('1')
            setChangeFlag(true)
          }}
          sx={{
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            borderRadius: 2.5,
            background: maxSlippageValue === '1' ? 'linear-gradient(270deg, #7AE8F1 0%, #29A0FF 100%)' : '',
            cursor: 'pointer',
            height: '42px',
            width: '42px',
            fontSize: '12px',
          }}
        >
          1%
        </Box>
        <Box
          sx={{
            flex: 1.2,
            display: 'flex',
            alignItems: 'center',
            borderRadius: 2,
            border:
              focusFlag ||
              (maxSlippageValue > 0 &&
                maxSlippageValue !== '0.5' &&
                maxSlippageValue !== '0.1' &&
                maxSlippageValue !== '1')
                ? '1px solid rgba(110, 214, 243, 1)'
                : '1px solid rgba(207, 216, 226, 0.5)',
            p: 1,
          }}
        >
          <TextField
            placeholder="1"
            multiline
            maxRows={5}
            size="small"
            onBlur={() => setFocusFlag(false)}
            onFocus={() => setFocusFlag(true)}
            value={maxSlippageValue}
            onChange={(e) => {
              const inputValue = e.target.value
              let formattedValue = ''
              const cleanedValue = inputValue.replace(/[^\d.]/g, '')
              if (cleanedValue.includes('.')) {
                const parts = cleanedValue.split('.')
                parts[1] = parts[1]?.slice(0, 2)
                formattedValue = parts.join('.')
              } else {
                formattedValue = cleanedValue
              }
              setChangeFlag(true)

              if (Number(e.target.value) < 0 || e.target.value === '') {
                setMaxSlippageValue('0')
                return
              }
              if (Number(e.target.value) > 49.9) {
                setMaxSlippageValue('49.9')
                return
              }
              setMaxSlippageValue(
                formattedValue.includes('.') ? formattedValue.toString() : parseFloat(formattedValue).toString(),
              )
            }}
            sx={{
              '& .MuiInputBase-input': {
                textAlign: 'right',
                color: !chageFlag ? 'rgb(0 0 0 / 20%)' : '',
              },
              '& .MuiInputBase-input::placeholder': {
                textAlign: 'right',
              },
            }}
            variant="standard"
            fullWidth
            InputProps={{
              disableUnderline: true,
              sx: {
                padding: 0,
              },
              endAdornment: (
                <InputAdornment position="end">
                  <Typography sx={{ textAlign: 'left', fontSize: 14, fontWeight: 400, color: 'rgba(0,0,0,1)' }}>
                    %
                  </Typography>
                </InputAdornment>
              ),
            }}
          />
        </Box>
      </Box>
      <Box
        sx={{
          pl: 1.2,
          pr: 1.2,
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
          mt: 2,
          mb: 1,
        }}
      >
        <Typography sx={{ fontSize: 14, fontWeight: 400, color: 'rgba(0, 0, 0, 0.5)' }}>
          Transaction deadline
        </Typography>
        <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
          {/* <Typography sx={{ fontSize: 14, fontWeight: 500 }}>10m</Typography>
          <KeyboardArrowUpIcon sx={{ width: 16, height: 16 }} /> */}
        </Box>
      </Box>
      <Box
        sx={{
          height: 50,
          flex: 1,
          display: 'flex',
          alignItems: 'center',
          borderRadius: 2,
          border: '1px solid rgba(207, 216, 226, 0.5)',
          p: 1,
          ml: 1.2,
          mr: 1.2,
        }}
      >
        <TextField
          placeholder="10"
          multiline
          maxRows={5}
          size="small"
          value={deadline}
          onChange={(e) => {
            setChangeDedFlag(true)
            setDeadlineValue(e.target.value)
          }}
          sx={{
            '& .MuiInputBase-input': {
              textAlign: 'right',
              color: !chageDedFlag ? 'rgb(0 0 0 / 20%)' : '',
            },
            '& .MuiInputBase-input::placeholder': {
              textAlign: 'right',
            },
          }}
          variant="standard"
          fullWidth
          InputProps={{
            disableUnderline: true,
            sx: {
              padding: 0,
            },
            endAdornment: (
              <InputAdornment position="end">
                <Typography sx={{ fontSize: 14, fontWeight: 400, color: 'rgba(0, 0, 0, 1)' }}>minutes</Typography>
              </InputAdornment>
            ),
          }}
        />
      </Box>
    </Menu>
  )
}

export default TradeSettings
