import { useWeb3React } from 'web3-react-core'
import { useCallback } from 'react'
import { ethers } from 'ethers'
import { getContract, getSigner } from 'utils/web3React'
import ERC20 from 'config/abi/erc20.json'
import useSystemParams from './useSystemParams'

export function useCheckApprove() {
  const { active } = useWeb3React()
  const {signer} = useSystemParams()

  const handleApprove = useCallback(
    async (account: string, spender: string, target: string) => {
      if (active) {
        try {
          // TODO rpc url should be from the chainId

          // const { signer } = getProviderAndSigner(INFURA_NETWORK_URLS[SupportedChainId.BLAST_SEPPLIA], account, SupportedChainId.BLAST_SEPPLIA)
          const contract = getContract(signer, ERC20, spender)
          if (contract) {
            const allowance = await contract.allowance(account, target)
            return allowance
          }
        } catch (error) {
          console.error('Failed to check approval', error)
          return null
        }
      }
      return null
    },
    [active, signer],
  )

  return { onCheckApprove: handleApprove }
}




export function useApprove(owner, spender) {
  const { active, library, chainId } = useWeb3React()

  const handleApprove = useCallback(
    async (target) => {
      if (active && chainId) {
        const signer = getSigner(library, owner)

        const contract = getContract(signer, ERC20, spender && typeof spender === 'object' ? spender[chainId] : spender)

        if (contract) {
          return contract.approve(target, ethers.constants.MaxUint256)
        }
      }
      return null
    },
    [active, chainId, library, owner, spender],
  )

  return { handleApprove }
}
