import { useAppDispatch, useAppSelector } from 'state'
import { useWeb3React } from 'web3-react-core'
import { useApprove, useCheckApprove } from 'hooks/useApprove'
import { useCallback, useEffect, useMemo, useState } from 'react'
import tokens from 'config/constants/tokens'
import useToast from 'hooks/useToast'
import { usePositionInfo } from 'hooks/news/usePoistion'
import { ContractTransaction } from 'ethers'
import { SupportedChainId } from 'config/constants/chains'
import { chainConfigs, valueToKeyMap } from 'config/constants/tokenConfig'
import { ToastTitle } from 'config/constants/contants'
import {
  ActionType,
  NeetralizePendingAction,
  PendingAction,
  TokenType,
  updateActiveAmount,
  updateActionInfo,
  updateActiveActionIndex,
  setShortPendingAction,
  updateActiveTab,
  updateShortRateInfo,
} from './shortSlice'

export function useShortActiveTokenApprove() {
  const { account, chainId } = useWeb3React()
  const activeInfo = useAppSelector((state) => state.short.activeInfo)
  // const { position } = useCurrentCpt()
  const { currentPosition } = usePositionInfo()
  const { onCheckApprove } = useCheckApprove()
  const { handleApprove } = useApprove(
    account,
    activeInfo.token ? chainConfigs[chainId as SupportedChainId].tokens[valueToKeyMap[activeInfo.token]].address : '0',
  )
  const [approveStatus, setApproveStatus] = useState(false)
  const [checkApproveLoading, setCheckApproveLoading] = useState(false)
  const { toastError } = useToast()

  useEffect(() => {
    if (activeInfo.token) {
      setCheckApproveLoading(true)
      onCheckApprove(
        account,
        chainConfigs[chainId as SupportedChainId].tokens[valueToKeyMap[activeInfo.token]].address,
        currentPosition.positionAddress,
      )
        .then((res) => {
          if (res && res._hex > 0) {
            setApproveStatus(true)
          } else {
            setApproveStatus(false)
          }
        })
        .catch(() => {
          setApproveStatus(false)
        })
        .finally(() => {
          setCheckApproveLoading(false)
        })
    }

    // console.log('valueToKeyMap[activeInfo.token]', activeInfo.token, valueToKeyMap[activeInfo.token])
  }, [account, activeInfo, chainId, currentPosition.positionAddress, onCheckApprove])
  const onApprove = useCallback(async () => {
    try {
      setApproveStatus(true)
      const info = await handleApprove(currentPosition.positionAddress)

      return info as ContractTransaction
    } catch (error: any) {
      setApproveStatus(false)
      toastError(ToastTitle.failed, `${error.reason}`)
      throw error
    }
  }, [handleApprove, currentPosition.positionAddress, toastError])

  return { approveStatus, onApprove, checkApproveLoading }
}

// short active action and info
export function useShortActiveAction() {
  const dispatch = useAppDispatch()
  const { activeAmount, activeInfo, activeIndex } = useAppSelector((state) => state.short)
  const { pendingActionList } = useShortPendingAction()
  const setActionInfo = useCallback(
    (data: { token?: string; tokenType?: TokenType } = {}) => {
      dispatch(updateActionInfo({ activeInfo: data }))
      const _index = pendingActionList.findIndex(
        (item) => item.token === data.token && item.tokenType === data.tokenType,
      )
      if (_index >= 0) {
        dispatch(updateActiveActionIndex({ index: _index }))
      }
    },
    [dispatch, pendingActionList],
  )
  const setActiveTab = useCallback(
    (index: number) => {
      dispatch(updateActiveTab({ index }))
    },
    [dispatch],
  )
  const setActionIndex = useCallback(
    (index: number) => {
      dispatch(updateActiveActionIndex({ index }))
    },
    [dispatch],
  )
  const setActiveAmount = useCallback(
    (amount: any) => {
      dispatch(updateActiveAmount({ amount }))
    },
    [dispatch],
  )
  const clearActive = useCallback(() => {
    setActionInfo()
    setActiveAmount('')
    setActionIndex(-1)
  }, [setActionIndex, setActionInfo, setActiveAmount])

  // const { approveStatus, onApprove } = useShortActiveTokenApprove()
  const _activeInfo = useMemo(() => {
    if (activeInfo?.token) {
      return {
        ...activeInfo,
        amount: activeAmount,
        activeIndex,
        // approveStatus,
      }
    }
    return { token: null, amount: '', tokenType: null, activeIndex: -1, approveStatus: false }
  }, [activeAmount, activeIndex, activeInfo])

  const onChangeCollateralIndex = useCallback(
    ({ ratio, coverage }) => {
      dispatch(
        updateShortRateInfo({
          rateInfo: {
            rate: ratio,
            coverage,
          },
        }),
      )
    },
    [dispatch],
  )

  return {
    setActionInfo,
    setActiveAmount,
    clearActive,
    setActiveTab,
    activeInfo: _activeInfo,
    setActionIndex,
    onChangeCollateralIndex,
  }
}

export function useShortPendingAction() {
  const dispatch = useAppDispatch()
  const { pendingActions } = useAppSelector((state) => state.short)
  const _pendingActions = useMemo(() => new Map(pendingActions), [pendingActions])
  // add action
  const addPendingAction = useCallback(
    (data: Pick<PendingAction, 'amount' | 'price' | 'token' | 'tokenType'>) => {
      const _actions = _pendingActions.get(data.token) || []
      _pendingActions.set(data.token, [..._actions, { ...data, type: ActionType.ADD }])

      dispatch(setShortPendingAction({ actions: _pendingActions }))
    },
    [dispatch, _pendingActions],
  )
  // delete action
  const deletePendingAction = useCallback(
    (token: string, tokenType: TokenType) => {
      const _actions = [...(_pendingActions.get(token) || [])]
      const _nextList = _actions.filter((item) => item.tokenType !== tokenType)
      _pendingActions.set(token, _nextList)

      dispatch(setShortPendingAction({ actions: _pendingActions }))
    },
    [dispatch, _pendingActions],
  )
  // clear all action
  const clearAllPendingAction = useCallback(() => {
    dispatch(setShortPendingAction({ actions: new Map() }))
  }, [dispatch])

  // update action
  const updatePendingAction = useCallback(
    (data: Pick<PendingAction, 'amount' | 'price' | 'token' | 'tokenType'>) => {
      const _actions = [...(_pendingActions.get(data.token) || [])]
      const _index = _actions.findIndex((item) => item.tokenType === data.tokenType)
      _actions.splice(_index, 1, { ...data, type: ActionType.ADD })
      _pendingActions.set(data.token, _actions)

      dispatch(setShortPendingAction({ actions: _pendingActions }))
    },
    [_pendingActions, dispatch],
  )

  const updateNeutralizePendingAction = useCallback(
    (data: Pick<NeetralizePendingAction, 'amount' | 'price' | 'token' | 'tokenType' | 'neutralizeType'>) => {
      const _actions = [...(_pendingActions.get(data.token) || [])]
      const _index = _actions.findIndex((item) => item.neutralizeType === data.neutralizeType)
      _actions.splice(_index, 1, { ...data, type: ActionType.ADD })
      _pendingActions.set(data.token, _actions)

      dispatch(setShortPendingAction({ actions: _pendingActions }))
    },
    [_pendingActions, dispatch],
  )

  const deleteNeutralizePendingAction = useCallback(
    (token: string, tokenType: any) => {
      const _actions = [...(_pendingActions.get(token) || [])]
      const _nextList = _actions.filter((item) => item.neutralizeType !== tokenType)
      _pendingActions.set(token, _nextList)

      dispatch(setShortPendingAction({ actions: _pendingActions }))
    },
    [dispatch, _pendingActions],
  )

  // get token type
  const pendingTokenTypes = useMemo(() => {
    const _map = new Map<string, TokenType[]>()
    pendingActions.forEach((item, index) => {
      _map.set(
        index,
        item.map((i) => i.tokenType),
      )
    })
    return _map
  }, [pendingActions])

  const pendingActionList = useMemo(() => {
    return Array.from(pendingActions.values()).flat()
  }, [pendingActions])

  const tokenAmount = useMemo(() => {
    const _obg = {}
    pendingActionList.forEach((item) => {
      if (item.tokenType === TokenType.SUPPLY || item.tokenType === TokenType.WithDraw) {
        _obg[item.token] = item.amount
      }
    })
    return _obg
  }, [pendingActionList])

  const shortAmount = useMemo(() => {
    let _amount = 0
    pendingActionList.forEach((item) => {
      if (item.tokenType === TokenType.SHORT) {
        _amount = item.amount
      }
    })
    return _amount
  }, [pendingActionList])

  const neutralizeAmount = useMemo(() => {
    let _amount = 0
    pendingActionList.forEach((item) => {
      if (item.tokenType === TokenType.NEUTRALIZE) {
        _amount += item.amount
      }
    })
    return _amount
  }, [pendingActionList])

  const supplyAllAmount = useMemo(() => {
    let _amount = 0
    pendingActionList.forEach((item) => {
      if (item.tokenType === TokenType.SUPPLY) {
        _amount += item.amount
      }
    })
    return _amount
  }, [pendingActionList])

  const supplyAmount = useMemo(() => {
    const _amount = new Map<string, number>()
    pendingActions.forEach((items) => {
      const item = items.filter((i) => i.tokenType === TokenType.SUPPLY)[0]
      if (item) {
        _amount.set(item.token, item.amount)
      }
    })
    return _amount
  }, [pendingActions])
  return {
    addPendingAction,
    clearAllPendingAction,
    deletePendingAction,
    updatePendingAction,
    deleteNeutralizePendingAction,
    updateNeutralizePendingAction,
    pendingTokenTypes,
    pendingActions,
    pendingActionList,
    tokenAmount,
    shortAmount,
    supplyAmount,
    supplyAllAmount,
    neutralizeAmount,
  }
}

export function useShortInfo() {
  const { rateInfo, activeTab, activeInfo } = useAppSelector((state) => state.short)

  const dispatch = useAppDispatch()
  const onChangeCollateralIndex = useCallback(
    ({ ratio, coverage }) => {
      dispatch(
        updateShortRateInfo({
          rateInfo: {
            rate: ratio,
            coverage,
          },
        }),
      )
    },
    [dispatch],
  )
  return {
    rateInfo,
    onChangeCollateralIndex,
    activeTab,
    activeInfo,
  }
}
