import React, { useCallback, useEffect, useMemo, useState } from 'react'
import { MarkSlider, PrettoSlider, PrettoSlidertwo, SliderWrapper, SlidtRangeWrapper } from './StyleCommon'

export default function CustomizedSlider(props: { value: any }) {
  const { value } = props

  const [rateInfo, setRateInfo] = useState<{
    rate: number | string
    coverage: number | string
  }>({
    rate: 0,
    coverage: 0,
  })
  useEffect(() => {
    if (value) {
      if (value?.marks1 && value?.marks1.length > 0) {
        setRateInfo({
          rate: value?.defaultvalue,
          coverage: 0,
        })
      }else {
        setRateInfo({
          rate: 0,
          coverage: value?.defaultvalue,
        })
      }
    }
  }, [value])
  const computedValueRatio = useCallback((val: any) => {
    let _value = val

    if (_value === 'Infinity' || _value === Infinity || _value === '+∞') {
      return '+∞'
    }

    _value = Number(_value)

    if (_value === 0 || !_value) {
      return 0
    }

    if (_value && _value.toString().indexOf('%') !== -1) {
      return `${_value.replace(/%/g, '')}%`
    }

    return `${_value}%`
  }, [])

  const computedValueCoverage = useMemo(() => {
    const _value = rateInfo.coverage
    if (_value === 'Infinity' || _value === Infinity || _value === '+∞') {
      return '+∞'
    }

    if (_value === 0 || !_value) {
      return 0
    }

    return `${_value}`
  }, [rateInfo.coverage])
  const showInfo = useMemo(() => {
    const _coverage =
      (rateInfo.coverage as any) === 'Infinity' ||
      (rateInfo.coverage as any) === 'N/A' ||
      (rateInfo.coverage as any) === Infinity ||
      rateInfo.coverage === '+∞' ||
      (rateInfo.coverage as any).toString() === 'NAN' ||
      Number(rateInfo.coverage) > 300
        ? 300
        : Number(rateInfo.coverage)

    const _coverage1 = _coverage > 0 ? _coverage : 0

    const _coverage2 = _coverage1 <= 10 ? _coverage1 * 11 : (_coverage1 - 10) * 6 + 100

    const _ratio =
      (rateInfo.rate as any) === 'Infinity' ||
      (rateInfo.rate as any) === Infinity ||
      rateInfo.rate === '+∞' ||
      (rateInfo.rate as any).toString() === 'NAN' ||
      Number(rateInfo.rate) > 300
        ? 300
        : rateInfo.rate
    const _ratio1 = Number(_ratio) > 0 ? _ratio : 0
    const _ratio2 = Number(_ratio1) <= 110 ? Number(_ratio1) : (Number(_ratio1) - 100) * 3 + 100
    return {
      rate: _ratio2,
      coverage: _coverage2,
    }
  }, [rateInfo.coverage, rateInfo.rate])
  return (
    <>
      <div className="range_wrap">
        <SliderWrapper className="custom-slider-wrapper">
          <MarkSlider
            valueLabelDisplay="off"
            aria-label="pretto slider"
            value={Number(rateInfo.rate)}
            marks={value.marks1}
            max={300}
          />
        </SliderWrapper>
        {value.marks1.length > 0 ? (
          <SlidtRangeWrapper value={showInfo.rate}>
            <PrettoSlider
              disabled
              aria-label="pretto slider"
              defaultValue={120}
              value={showInfo.rate}
              max={300}
              valueLabelDisplay="on"
              marks={value.marks}
              sx={{
                ".css-1qlnp9f-MuiSlider-root .MuiSlider-markLabel[data-index='1']": {
                  pr: 70,
                },
                ".css-1qlnp9f-MuiSlider-root .MuiSlider-markLabel[data-index='2']": {
                  pl: 70,
                },
              }}
            />
            <div className="custom_range_flag">
              <div className="range_val">{`${computedValueRatio(rateInfo.rate)}`}</div>
            </div>
          </SlidtRangeWrapper>
        ) : (
          <SlidtRangeWrapper className="feature_item" value={Number(showInfo.coverage)}>
            <PrettoSlidertwo
              disabled
              aria-label="pretto slider"
              defaultValue={20}
              value={showInfo.coverage}
              valueLabelDisplay="on"
              marks={value.marks}
              max={300}
            />
            <div className="custom_range_flag">
              <div className="range_val">{computedValueCoverage}</div>
            </div>
          </SlidtRangeWrapper>
        )}
      </div>
    </>
  )
}
