import {
  Box,
  Typography,
  Button,
  TableCell,
  TableRow,
  Badge,
  IconButton,
  Paper,
  Table,
  TableBody,
  TableContainer,
  TableHead,
} from '@mui/material'
import React, { useCallback, useMemo, useState } from 'react'
import useHashPowerToken from 'hooks/useHashPowerToken'
import { useWeb3React } from 'web3-react-core'
import Wallet from 'components/Menu/icons/Wallet'
import { AvatarIcon, Fbtc20Icon } from 'components/uikit'
import { useShortActiveAction, useShortActiveTokenApprove, useShortPendingAction } from 'state/Short/hooks'
import PositionABI from 'config/abi/Position.json'
import { ethers, utils } from 'ethers'
import ClearIcon from '@mui/icons-material/Clear'
import contracts from 'config/constants/contracts'
import useToast from 'hooks/useToast'
import useSystemParams from 'hooks/useSystemParams'
import { TokenType } from 'state/Short/shortSlice'
import { TokenUtils, toLargeUnits, toSmallUnits } from 'utils/transformHelper'
import styled from 'styled-components'
import { useComputedIndexAction, usePositionAction, usePositionInfo } from 'hooks/news/usePoistion'
import { commonTokens, valueToKeyMap } from 'config/constants/tokenConfig'
import { ToastTitle } from 'config/constants/contants'
import { TransactionSubmittedInfo } from 'components/TransactionInfo'
import Logo from 'components/Logo'
import Assets from 'assets'
import NeutralizeEstimatedDashBoard from './NeutralizeEstimatedDashBoard'
import { ActionButton, InputBox, SelectTypeWrap, TipInfo, pendingStyle } from './StyleCommon'
import WarnRedIcon from './icon/WarnRedIcon'
import WarnGrayIcon from './icon/WarnGrayIcon'

const AvailableText = styled('div')`
  font-family: PingFang SC;
  font-size: 12px;
  font-weight: 400;
  line-height: 16.8px;
  text-align: right;
`
const AvailableBox = styled('div')`
  position: absolute;
  top: 33px;
`

const TableBox: React.FC<{
  setNeutralizeType: (val: string) => any
}> = ({ setNeutralizeType }) => {
  const [hoveredRow, setHoveredRow] = useState<number>(-1)

  const { setActionInfo, setActiveAmount, setActionIndex } = useShortActiveAction()

  const { pendingActionList, clearAllPendingAction, deleteNeutralizePendingAction } = useShortPendingAction()
  const handleMouseEnter = (index: number) => {
    setHoveredRow(index)
  }

  const handleMouseLeave = () => {
    setHoveredRow(-1)
  }
  return (
    <TableContainer
      component={Paper}
      sx={{
        backgroundColor: 'transparent',
        border: 'none',
        boxShadow: 'none',
      }}
    >
      <Table aria-label="simple table">
        <TableHead>
          <TableRow sx={pendingStyle.tableHeader}>
            <TableCell sx={pendingStyle.tableHeadCell}>{pendingActionList.length} Pending Actions</TableCell>
            <TableCell align="right" sx={pendingStyle.tableHeadCell}>
              <Button
                size="small"
                sx={pendingStyle.clearAll}
                onClick={() => {
                  clearAllPendingAction()
                }}
              >
                Clear All
              </Button>
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody sx={pendingStyle.tableBody}>
          {pendingActionList.map((row, index) => (
            <TableRow
              key={row.tokenType + row.token}
              sx={pendingStyle.tableBodyRow}
              onMouseEnter={() => handleMouseEnter(index)}
              onMouseLeave={handleMouseLeave}
              onClick={() => {
                setActionInfo({ token: row.token, tokenType: row.tokenType })
                setActiveAmount(row.amount)
                setActionIndex(index)
                setNeutralizeType(row.neutralizeType)
              }}
            >
              <TableCell sx={pendingStyle.tableBodyCell}>
                <Badge
                  color="error"
                  overlap="circular"
                  anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'right',
                  }}
                  sx={{
                    '.MuiBadge-badge': {
                      padding: 0,
                      minWidth: 10,
                      minHeight: 10,
                      height: 10,
                      position: 'absolute',
                      bottom: '20%',
                      right: '24%',
                      backgroundColor: 'rgba(110, 214, 243, 1)',
                      borderWidth: 1,
                      borderColor: 'rgba(235, 247, 255, 1)',
                      borderStyle: 'solid',
                    },
                  }}
                >
                  {
                  row.neutralizeType === 'wallet' ? <Logo token={Assets.Wallet} symbol='wallet' /> : <AvatarIcon />
                  }
                </Badge>
                <Typography sx={{ fontWeight: 600, marginLeft: '5px' }}>
                  {row.tokenType} {row.token}
                </Typography>
              </TableCell>
              <TableCell align="right" sx={{ p: 0, height: 66 }}>
                <Box sx={{ display: 'flex', justifyContent: 'end', alignItems: 'center' }}>
                  <Box>
                    <Typography sx={{ fontSize: 14 }}>{row.amount}</Typography>
                    {/* <Typography sx={{ fontSize: 12, color: 'rgba(0, 0, 0, 0.5)' }}>
                      {row.price} {token}
                    </Typography> */}
                  </Box>
                  <Box
                    sx={{
                      transition: 'all 0.3s',
                      width: hoveredRow === index ? 32 : 0,
                      display: 'flex',
                      alignItems: 'center',
                    }}
                  >
                    <IconButton
                      onClick={(e) => {
                        e.stopPropagation()
                        deleteNeutralizePendingAction(row.token, row.neutralizeType)
                      }}
                      sx={{
                        transition: 'all 0.3s',
                        opacity: hoveredRow === index ? 1 : 0,
                        color: 'rgba(0, 0, 0, 1)',
                      }}
                    >
                      <ClearIcon sx={{ width: 16, height: 16 }} />
                    </IconButton>
                  </Box>
                </Box>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  )
}

const { ActionUser } = contracts
const ActionUserInterface = new utils.Interface(ActionUser.abi)
function NeutralizeRightBox() {
  const { chainId } = useWeb3React()
  const { walletTokenBalance, setRefresh } = useHashPowerToken()
  const [neutralizeType, setNeutralizeType] = useState('wallet')
  // const [neutralizeAmount, setNeutralizeAmount] = useState<any>('')

  const {
    addPendingAction,
    updateNeutralizePendingAction,
    deleteNeutralizePendingAction,
    clearAllPendingAction,
    pendingActionList,
  } = useShortPendingAction()

  const { signer } = useSystemParams()
  const { toastSuccess, toastError, toastInfo } = useToast()
  const [availableShowFlag, setAvailableShowFlag] = useState(false)
  const [approveFlag, setApproveFlag] = useState(false)

  const { activeInfo, setActiveAmount, setActiveTab, clearActive } = useShortActiveAction()
  const { onApprove, checkApproveLoading, approveStatus } = useShortActiveTokenApprove()
  const isUpdate = useMemo(() => activeInfo.activeIndex !== -1, [activeInfo.activeIndex])

  const [neutralizeLoading, setNeutralizeLoading] = useState(false)
  const [, setDisabledAction] = useState(true)

  const { currentPositionTokenInfo, currentMintToken, currentPosition } = usePositionInfo()

  const { handleUpdatePosition } = usePositionAction()
  const { newNeutralizeFee } = useComputedIndexAction()

  const avatarAssetList = useMemo(() => {
    if (currentPositionTokenInfo.allowedTokens) {
      const { balances, tokens } = currentPositionTokenInfo.allowedTokens
      return Object.keys(balances).reduce((acc, key) => {
        return {
          ...acc,
          [key]: toLargeUnits(balances[key], tokens[key].decimals, true) as number,
        }
      }, {})
    }
    return Object.keys(valueToKeyMap).reduce((acc, key) => {
      return {
        ...acc,
        [key]: 0,
      }
    }, {})
  }, [currentPositionTokenInfo.allowedTokens])

  const maxValue = useMemo(() => {
    const _maxVal = Number(toLargeUnits(currentPosition.balance, currentPosition.mintTokenInfo.decimals))
    return _maxVal
  }, [currentPosition.balance, currentPosition.mintTokenInfo.decimals])

  const disableFlag = useMemo(() => {
    const { amount } = activeInfo
    if (amount <= 0) {
      return true
    }
    if (amount > 0) {
      return neutralizeType === 'wallet'
        ? amount > walletTokenBalance[activeInfo.token] || amount > maxValue
        : amount > avatarAssetList[activeInfo.token] || amount > maxValue
    }
    return false
  }, [activeInfo, avatarAssetList, maxValue, neutralizeType, walletTokenBalance])

  const submitDisable = useMemo(() => {
    return pendingActionList.length === 0
  }, [pendingActionList])
  const haveTokenFlag = useMemo(() => {
    return pendingActionList.some((item) => {
      if (item && (item as any).neutralizeType) {
        return (item as any).neutralizeType === neutralizeType
      }
      return false
    })
  }, [neutralizeType, pendingActionList])

  const onMax = () => {
    if (neutralizeType === 'avatar') {
      if (avatarAssetList && avatarAssetList[currentMintToken]) {
        let actionVal = avatarAssetList[currentMintToken]

        if (avatarAssetList[currentMintToken] > maxValue) {
          actionVal = maxValue
        }
        setActiveAmount(actionVal)
        if (actionVal > 0) {
          setDisabledAction(false)
        } else {
          setDisabledAction(true)
        }
      } else {
        setDisabledAction(true)
      }
    }
    if (neutralizeType === 'wallet') {
      let actionVal = walletTokenBalance[currentMintToken]

      if (walletTokenBalance[currentMintToken] > maxValue) {
        actionVal = maxValue
      }
      setActiveAmount(actionVal)
      if (actionVal > 0) {
        setDisabledAction(false)
      } else {
        setDisabledAction(true)
      }
    }
  }

  const onNeutralize = useCallback(async () => {
    pendingActionList.forEach(async (item) => {
      if (item.tokenType === TokenType.NEUTRALIZE) {
        setNeutralizeLoading(true)
        const resData = ActionUserInterface.encodeFunctionData('neutralizetBalance', [
          toSmallUnits(item.amount.toString(), currentPosition.mintTokenInfo.decimals) || '0',
          item.neutralizeType === 'wallet',
        ])

        try {
          const posContract = new ethers.Contract(currentPosition.positionAddress, PositionABI, signer)
          const info = await posContract.delegateCall(ActionUser.address[chainId], resData)

          if (info) {
            toastInfo(
              ToastTitle.submited,
              <TransactionSubmittedInfo content="Waiting for confirmation" txHash={info.hash} />,
            )
            const result = await info.wait()
            if (result) {
              setRefresh((state) => !state)
              toastSuccess(
                ToastTitle.success,
                <TransactionSubmittedInfo content="The transaction has been successfully" txHash={info.hash} />,
              )
              clearAllPendingAction()
              setActiveAmount('')
              setNeutralizeLoading(false)
              await handleUpdatePosition()
            }
          }
        } catch (e: any) {
          setNeutralizeLoading(false)
          toastError(ToastTitle.failed, e?.reason)
        }
      }
    })
    // return
  }, [
    pendingActionList,
    currentPosition.mintTokenInfo.decimals,
    currentPosition.positionAddress,
    signer,
    chainId,
    toastInfo,
    setRefresh,
    toastSuccess,
    clearAllPendingAction,
    setActiveAmount,
    handleUpdatePosition,
    toastError,
  ])

  const estimateNeutralizeFeeWarningFlag = useCallback(() => {
    if (neutralizeType === 'wallet') {
      return (
        walletTokenBalance &&
        newNeutralizeFee > walletTokenBalance[currentPosition.mintTokenInfo.settlementCurrency.symbol]
      )
    }
    if (neutralizeType === 'avatar') {
      return (
        avatarAssetList && newNeutralizeFee > avatarAssetList[currentPosition.mintTokenInfo.settlementCurrency.symbol]
      )
    }
    return false
  }, [
    avatarAssetList,
    currentPosition.mintTokenInfo.settlementCurrency.symbol,
    neutralizeType,
    newNeutralizeFee,
    walletTokenBalance,
  ])

  const tokenAmountBalanceWarningFlag = useCallback(
    (type: string) => {
      if (neutralizeType === 'wallet' && type === 'wallet') {
        return walletTokenBalance && activeInfo.amount > walletTokenBalance[currentMintToken] ? 'red' : ''
      }
      if (neutralizeType === 'avatar' && type === 'avatar') {
        return avatarAssetList && activeInfo.amount > avatarAssetList[currentMintToken] ? 'red' : ''
      }
      return ''
    },
    [neutralizeType, walletTokenBalance, activeInfo.amount, currentMintToken, avatarAssetList],
  )

  const walletNeutralizeFeeWarningFlag = useCallback(
    (type: string) => {
      const { settlementCurrency } = currentPosition.mintTokenInfo
      if (neutralizeType === type) {
        return walletTokenBalance && newNeutralizeFee > walletTokenBalance[settlementCurrency.symbol] ? 'red' : ''
      }
      if (neutralizeType === type) {
        return avatarAssetList && newNeutralizeFee > avatarAssetList[settlementCurrency.symbol] ? 'red' : ''
      }
      return ''
    },
    [avatarAssetList, currentPosition.mintTokenInfo, neutralizeType, newNeutralizeFee, walletTokenBalance],
  )

  return (
    <Box sx={{ width: '100%' }}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <Typography sx={{ fontSize: 16, fontWeight: 500 }}>Neutralize {currentMintToken}</Typography>
      </Box>

      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mt: 1.6 }}>
        <Box
          sx={{
            height: '45px',
            transition: '300ms all',
            width: '100%',
          }}
        >
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1.2, width: '100%' }}>
            {/* <Fbtc20Icon style={{ width: 42, height: 42 }} /> */}
            <Logo token={commonTokens[valueToKeyMap[currentMintToken]].logo} imgprops={{style:{width: 42, height: 42}}} symbol={currentMintToken} />
            <div
              style={{
                width: '100%',
                // transition: 'all 300ms',
                position: 'relative',
              }}
            >
              <InputBox>
                <input
                  type="number"
                  // placeholder={walletTokenBalance[token] }
                  placeholder="0"
                  value={activeInfo?.amount ?? ''}
                  onBlur={() => {
                    if (activeInfo?.amount.toString() === '' || activeInfo?.amount < 0) {
                      setAvailableShowFlag(false)
                    }
                  }}
                  onFocus={() => setAvailableShowFlag(true)}
                  onChange={(event) => {
                    const inputValue = event.target.value
                    let formattedValue = ''
                    const cleanedValue = inputValue.replace(/[^\d.]/g, '')
                    if (cleanedValue.includes('.')) {
                      const parts = cleanedValue.split('.')
                      parts[1] = parts[1]?.slice(0, currentPosition.mintTokenInfo.decimals)
                      formattedValue = parts.join('.')
                    } else {
                      formattedValue = cleanedValue
                    }

                    setActiveAmount(formattedValue || '')
                  }}
                />
              </InputBox>
              {activeInfo?.token && availableShowFlag ? (
                <AvailableBox>
                  <Box
                    sx={{
                      display: 'flex',
                      alignItems: 'center',
                      gap: 1,
                      width: '100%',
                      color: 'rgba(0,0,0,.5)',
                      paddingLeft: '4px',
                      position: 'relative',
                    }}
                  >
                    <AvailableText>
                      {neutralizeType === 'wallet'
                        ? walletTokenBalance[activeInfo?.token]
                        : avatarAssetList[activeInfo?.token]}{' '}
                      Available
                    </AvailableText>
                  </Box>
                </AvailableBox>
              ) : (
                <></>
              )}
            </div>
          </Box>
        </Box>
        <Box>
          <Button
            size="small"
            sx={{
              textTransform: 'none',
              borderRadius: 1.8,
              color: 'rgba(110, 214, 243, 1)',
            }}
            onClick={() => onMax()}
          >
            Max
          </Button>
        </Box>
      </Box>

      <SelectTypeWrap>
        <p className="title-neutralize">Neutralize the asset from</p>
        <div className="content">
          <div
            className={`item_wrap ${neutralizeType === 'wallet' ? 'active' : ''}`}
            onClick={() => {
              setNeutralizeType('wallet')
              const neutralize = pendingActionList.find((it) => it.neutralizeType === 'avatar')
              console.log('neutralize', neutralize)
              if (neutralize) {
                deleteNeutralizePendingAction(neutralize.token, neutralize.neutralizeType)
              } else if ((activeInfo.amount > 0 && isUpdate) || haveTokenFlag) {
                addPendingAction({
                  currentMintToken,
                  tokenType: TokenType.NEUTRALIZE,
                  amount: Number(activeInfo.amount),
                  neutralizeType: 'wallet',
                  price: 1,
                } as any)
              }
            }}
            onKeyUp={() => null}
            tabIndex={0}
            role="button"
          >
            <div className="item wallet">
              <h4>
                <Wallet /> My Wallet
              </h4>
              <p>
                <span>{currentMintToken}</span>
                <span
                  style={{
                    color: tokenAmountBalanceWarningFlag('wallet'),
                  }}
                >
                  {(walletTokenBalance && TokenUtils.formatFourDecimals(walletTokenBalance[currentMintToken])) || 0}
                </span>
              </p>
              <p>
                <span>{currentPosition.mintTokenInfo.settlementCurrency.symbol}</span>
                <span
                  style={{
                    color: walletNeutralizeFeeWarningFlag('wallet'),
                  }}
                >
                  {(walletTokenBalance &&
                    TokenUtils.formatFourDecimals(
                      walletTokenBalance[currentPosition.mintTokenInfo.settlementCurrency.symbol],
                    )) ||
                    0}
                </span>
              </p>
            </div>
          </div>

          <div
            className={`item_wrap ${neutralizeType === 'avatar' ? 'active' : ''}`}
            onClick={() => {
              setNeutralizeType('avatar')
              const neutralize = pendingActionList.find((it) => it.neutralizeType === 'wallet')
              console.log('neutralize', neutralize)
              if (neutralize) {
                deleteNeutralizePendingAction(neutralize.token, neutralize.neutralizeType)
              } else if ((activeInfo.amount > 0 && isUpdate) || haveTokenFlag) {
                addPendingAction({
                  token: currentMintToken,
                  tokenType: TokenType.NEUTRALIZE,
                  amount: Number(activeInfo.amount),
                  neutralizeType: 'avatar',
                  price: 1,
                } as any)
              }
            }}
            onKeyUp={() => null}
            tabIndex={0}
            role="button"
          >
            <div className="item avatar">
              <h4>
                <AvatarIcon />
                Collateral
              </h4>
              <p>
                <span>{currentMintToken}</span>
                <span
                  style={{
                    color: tokenAmountBalanceWarningFlag('avatar'),
                  }}
                >
                  {(avatarAssetList && TokenUtils.formatFourDecimals(avatarAssetList[currentMintToken])) || 0}
                </span>
              </p>
              <p>
                <span>{currentPosition.mintTokenInfo.settlementCurrency.symbol}</span>
                <span
                  style={{
                    color: walletNeutralizeFeeWarningFlag('wallet'),
                  }}
                >
                  {(avatarAssetList &&
                    TokenUtils.formatFourDecimals(
                      avatarAssetList[currentPosition.mintTokenInfo.settlementCurrency.symbol],
                    )) ||
                    0}
                </span>
              </p>
            </div>
          </div>
        </div>
      </SelectTypeWrap>

      <TipInfo>
        {estimateNeutralizeFeeWarningFlag() ? <WarnRedIcon /> : <WarnGrayIcon />}

        <p
          style={{
            color: estimateNeutralizeFeeWarningFlag() ? 'red' : '',
          }}
        >
          A 1.0% fee of the token value will be levied after the position is neutralized.
        </p>
      </TipInfo>

      <Box
        sx={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          gap: 3,
          mt: 1.6,
        }}
      >
        <ActionButton
          variant="contained"
          disableElevation
          disabled={neutralizeLoading}
          onClick={() => {
            clearAllPendingAction()
            clearActive()

            // clearActive()
            // setNeutralizeAmount('')
            setActiveTab(1)
          }}
        >
          Cancel
        </ActionButton>
        {checkApproveLoading ? (
          <ActionButton disabled variant="contained" disableElevation>
            Check Approving
          </ActionButton>
        ) : (
          <ActionButton
            variant="contained"
            disableElevation
            disabled={disableFlag || approveFlag || neutralizeLoading}
            onClick={async () => {
              const action = {
                token: currentMintToken as unknown as string,
                tokenType: TokenType.NEUTRALIZE,
                amount: Number(activeInfo.amount),
                neutralizeType,
                price: 1,
              }
              if (!approveStatus) {
                setApproveFlag(true)
                try {
                  const tx = await onApprove()
                  toastInfo('Approve Submitted')
                  const receipt = await tx.wait()
                  if (receipt.status === 1) {
                    addPendingAction(action)
                    clearActive()
                    setApproveFlag(false)
                    toastSuccess('Approved Successfully')
                  }
                } catch (err: any) {
                  setApproveFlag(false)
                }
              } else if (isUpdate || haveTokenFlag) {
                updateNeutralizePendingAction(action)
              } else if (activeInfo.amount > 0) {
                addPendingAction(action)
              }
            }}
          >
            {!approveStatus ? 'Approve & Add' : `${isUpdate || haveTokenFlag ? 'Update' : 'Add'} Action`}
          </ActionButton>
        )}
      </Box>
      {pendingActionList.length > 0 && (
        <Box>
          <TableBox
            setNeutralizeType={(value) => {
              setNeutralizeType(value)
            }}
          />
        </Box>
      )}

      {pendingActionList.length > 0 && (
        <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', mt: 3 }}>
          <ActionButton
            onClick={() => {
              onNeutralize()
            }}
            disabled={submitDisable || neutralizeLoading}
            variant="contained"
            disableElevation
          >
            Submit Transaction
          </ActionButton>
        </Box>
      )}
      <NeutralizeEstimatedDashBoard neutralizeType={neutralizeType} />
    </Box>
  )
}

export default NeutralizeRightBox
