import { Box, Typography } from '@mui/material'
import React, { useCallback, useEffect, useMemo } from 'react'
import SubdirectoryArrowRightIcon from '@mui/icons-material/SubdirectoryArrowRight'
import useHashPowerToken from 'hooks/useHashPowerToken'
import { useComputedIndexAction, usePositionInfo } from 'hooks/news/usePoistion'
import { valueToKeyMap } from 'config/constants/tokenConfig'
import { TokenUtils, toLargeUnits } from 'utils/transformHelper'
import { useShortActiveAction, useShortInfo, useShortPendingAction } from 'state/Short/hooks'
import { TokenType } from 'state/Short/shortSlice'
import { ShortThresholdValue } from 'config/constants/thresholdValue'

const NeutralizeEstimatedDashBoard: React.FC<{
  neutralizeType: string
}> = ({ neutralizeType }) => {
  const { walletTokenBalance } = useHashPowerToken()

  const { neutralizeAmount } = useShortPendingAction()
  const { activeInfo } = useShortActiveAction()
  const { activeTab } = useShortInfo()
  const { collateralIndex, currentPosition, currentPositionTokenInfo } = usePositionInfo()
  const {
    newEstimatedDailyDebt,
    newOutStandingDebt,
    newNeutralizeFee,
    newPrepaidInterestCoverage,
    newDisplayPrepaidInterestCoverage,
    newDisplayCollateralRatio,
    newCollateralRatio,
    onUpdateNeutralizeValue,
  } = useComputedIndexAction()

  const avatarAssetList = useMemo(() => {
    if (currentPositionTokenInfo.allowedTokens) {
      // eslint-disable-next-line @typescript-eslint/no-shadow
      const { balances, tokens } = currentPositionTokenInfo.allowedTokens
      return Object.keys(balances).reduce((acc, key) => {
        return {
          ...acc,
          [key]: toLargeUnits(balances[key], tokens[key].decimals, true) as number,
        }
      }, {})
    }
    return Object.keys(valueToKeyMap).reduce((acc, key) => {
      return {
        ...acc,
        [key]: 0,
      }
    }, {})
  }, [currentPositionTokenInfo.allowedTokens])

  const walletData = useMemo(() => {
    return [
      {
        id: 1,
        title: 'Collateral Ratio',
        type: 'ratio',
        value: {
          upVal: collateralIndex.displayRatio,
          downVal: newDisplayCollateralRatio,
        },
      },
      {
        id: 2,
        title: 'Prepaid Interest Coverage',
        type: 'coverage',
        value: {
          upVal: collateralIndex.displayCoverage,
          downVal:
            newPrepaidInterestCoverage > 0 || newPrepaidInterestCoverage !== Number.POSITIVE_INFINITY
              ? `≈ ${newDisplayPrepaidInterestCoverage}`
              : newDisplayPrepaidInterestCoverage,
        },
      },
      {
        id: 3,
        title: 'Prepaid Interest',
        type: 'prepaidInterest',
        value: {
          upVal: `${TokenUtils.formatFourDecimals(collateralIndex.prepaidInterest)} ${currentPosition.mintTokenInfo.settlementCurrency.symbol}`,
          downVal: `${TokenUtils.formatFourDecimals(collateralIndex.prepaidInterest)} ${currentPosition.mintTokenInfo.settlementCurrency.symbol}`,
        },
      },
      {
        id: 4,
        title: 'Daily Paid Interest Reference',
        type: 'dailyPaid',
        value: {
          upVal: `${TokenUtils.formatFourDecimals(collateralIndex.estimatedDailyDebt)} ${currentPosition.mintTokenInfo.settlementCurrency.symbol}`,
          downVal: `${TokenUtils.formatFourDecimals(Number(toLargeUnits(Number(TokenUtils.formatTokenAmount(newEstimatedDailyDebt, 0)), currentPosition.mintTokenInfo.settlementCurrency.decimals)))} ${currentPosition.mintTokenInfo.settlementCurrency.symbol}`,
        },
      },
      {
        id: 5,
        title: 'Outstanding Debt',
        type: 'debt',
        value: {
          upVal: toLargeUnits(currentPosition.balance, currentPosition.mintTokenInfo.decimals),
          downVal: toLargeUnits(newOutStandingDebt, currentPosition.mintTokenInfo.decimals),
        },
      },

      {
        id: 6,
        title: 'Estimate Neutralize Fee',
        type: 'neutralizeFee',
        value: {
          upVal: `0 ${currentPosition.mintTokenInfo.settlementCurrency.symbol}`,
          downVal: `≈ ${TokenUtils.formatFourDecimals(newNeutralizeFee)} ${currentPosition.mintTokenInfo.settlementCurrency.symbol}`,
        },
      },
    ]
  }, [collateralIndex.displayRatio, collateralIndex.displayCoverage, collateralIndex.prepaidInterest, collateralIndex.estimatedDailyDebt, newDisplayCollateralRatio, newPrepaidInterestCoverage, newDisplayPrepaidInterestCoverage, currentPosition.mintTokenInfo.settlementCurrency.symbol, currentPosition.mintTokenInfo.settlementCurrency.decimals, currentPosition.mintTokenInfo.decimals, currentPosition.balance, newEstimatedDailyDebt, newOutStandingDebt, newNeutralizeFee])

  const downValColor = useCallback(
    (item) => {
      const walletWbtc =
        neutralizeType === 'wallet' &&
        walletTokenBalance &&
        newNeutralizeFee > walletTokenBalance[currentPosition.mintTokenInfo.settlementCurrency.symbol]

      const avatarWbtc =
        neutralizeType === 'avatar' &&
        avatarAssetList &&
        newNeutralizeFee > avatarAssetList[currentPosition.mintTokenInfo.settlementCurrency.symbol]
      const colors =
        (item.type === 'ratio' && newCollateralRatio < ShortThresholdValue.ratio && walletWbtc) ||
        (item.type === 'ratio' && newCollateralRatio < ShortThresholdValue.ratio && avatarWbtc) ||
        (item.type === 'coverage' && newPrepaidInterestCoverage < ShortThresholdValue.days && avatarWbtc) ||
        (item.type === 'coverage' && newPrepaidInterestCoverage < ShortThresholdValue.days && walletWbtc)
          ? '#FF4D00'
          : '#2AA1FF'
      return ['prepaidInterest', 'dailyPaid', 'debt'].includes(item.type) ? '#2AA1FF' : colors
    },
    [
      avatarAssetList,
      currentPosition.mintTokenInfo.settlementCurrency.symbol,
      neutralizeType,
      newCollateralRatio,
      newNeutralizeFee,
      newPrepaidInterestCoverage,
      walletTokenBalance,
    ],
  )

  useEffect(() => {
    if (activeInfo?.tokenType === TokenType.NEUTRALIZE) {
      const val = Number(activeInfo?.amount) || 0
      onUpdateNeutralizeValue(val ? Number(`-${val}`) : 0)
    } else if (activeTab === 2) {
      const val = Number(neutralizeAmount) || 0
      onUpdateNeutralizeValue(val ? Number(`-${val}`) : 0)
    }
  }, [activeInfo, onUpdateNeutralizeValue, neutralizeAmount, activeTab])
  return (
    <Box
      sx={{
        bgcolor: 'rgba(222, 241, 255, 1)',
        borderRadius: 1.8,
        mt: 4,
        p: 2,
        display: 'flex',
        flexDirection: 'column',
        gap: 1.2,
      }}
    >
      {walletData.map((item: any) => {
        return (
          <Box
            sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', textAlign: 'right' }}
            key={item.id}
          >
            <Typography sx={{ fontSize: 12, color: 'rgba(0,0,0,0.5)', textAlign: 'right' }}>{item.title}</Typography>

            <Box>
              <Typography sx={{ fontSize: 12, color: 'rgba(0,0,0,0.6)', textAlign: 'right' }}>
                {item.value.upVal}
              </Typography>
              {(
                <Box sx={{ display: 'flex', alignItems: 'center', textAlign: 'right', justifyContent: 'end' }}>
                  <SubdirectoryArrowRightIcon sx={{ color: 'rgba(0,0,0,0.1)', width: 12, height: 12 }} />
                  <Typography
                    sx={{
                      fontSize: 10,
                      color: downValColor(item),
                      textAlign: 'right',
                    }}
                  >
                    {item.value.downVal}
                  </Typography>
                </Box>
              ) || null}
            </Box>
          </Box>
        )
      })}
    </Box>
  )
}
export default NeutralizeEstimatedDashBoard
