const AvailableText = styled('div')`
  font-family: PingFang SC;
  font-size: 12px;
  font-weight: 400;
  line-height: 16.8px;
  text-align: right;
`
const AvailableBox = styled('div')`
  position: absolute;
  top: 33px;
`
const getFullNum = (num, decimal) => {
  if (Number.isNaN(num)) {
    return num
  }
  const str = `${num}`
  if (!/e/i.test(str)) {
    return num
  }
  return num.toFixed(decimal).replace(/\.?0+$/, '')
}

const ActionList: React.FC = () => {
  const [hoveredRow, setHoveredRow] = useState<number>(-1)

  const { clearAllPendingAction, pendingActionList, deletePendingAction } = useShortPendingAction()
  const { setActionInfo, setActiveAmount, setActionIndex } = useShortActiveAction()
  const { currentMintToken } = usePositionInfo()
  const handleMouseEnter = (index: number) => {
    setHoveredRow(index)
  }

  const handleMouseLeave = () => {
    setHoveredRow(-1)
  }
  return (
    <TableContainer
      component={Paper}
      sx={{
        backgroundColor: 'transparent',
        border: 'none',
        boxShadow: 'none',
      }}
    >
      <Table aria-label="simple table">
        <TableHead>
          <TableRow sx={pendingStyle.tableHeader}>
            <TableCell sx={pendingStyle.tableHeadCell}>{pendingActionList.length} Pending Actions</TableCell>
            <TableCell align="right" sx={pendingStyle.tableHeadCell}>
              <Button
                size="small"
                sx={pendingStyle.clearAll}
                onClick={() => {
                  clearAllPendingAction()
                }}
              >
                Clear All
              </Button>
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody sx={pendingStyle.tableBody}>
          {pendingActionList.map((row, index) => (
            <TableRow
              key={row.tokenType + row.token}
              sx={pendingStyle.tableBodyRow}
              onMouseEnter={() => handleMouseEnter(index)}
              onMouseLeave={handleMouseLeave}
              onClick={() => {
                setActionInfo({ token: row.token, tokenType: row.tokenType })
                setActiveAmount(row.amount)
                setActionIndex(index)
              }}
            >
              <TableCell sx={pendingStyle.tableBodyCell}>
                <Badge
                  color="error"
                  overlap="circular"
                  badgeContent={
                    row.type === ActionType.ADD ? (
                      <AddIcon sx={{ width: 7, height: 7, color: '#000' }} />
                    ) : (
                      <RemoveIcon sx={{ width: 7, height: 7, color: '#000' }} />
                    )
                  }
                  anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'right',
                  }}
                  sx={{
                    '.MuiBadge-badge': {
                      padding: 0,
                      minWidth: 10,
                      minHeight: 10,
                      height: 10,
                      position: 'absolute',
                      bottom: '20%',
                      right: '24%',
                      backgroundColor: 'rgba(110, 214, 243, 1)',
                      borderWidth: 1,
                      borderColor: 'rgba(235, 247, 255, 1)',
                      borderStyle: 'solid',
                    },
                  }}
                >
                  {valueToKeyMap[row.token]}
                  {/* {IconMap[row.token]()} */}
                </Badge>
                <Typography sx={{ fontWeight: 600, marginLeft: '5px' }}>
                  {row.tokenType} {row.token}
                </Typography>
              </TableCell>
              <TableCell align="right" sx={{ p: 0, height: 66 }}>
                <Box sx={{ display: 'flex', justifyContent: 'end' }}>
                  <Box>
                    <Typography sx={{ fontSize: 14 }}>{row.amount}</Typography>
                    <Typography sx={{ fontSize: 12, color: 'rgba(0, 0, 0, 0.5)' }}>
                      {row.price} {currentMintToken}
                    </Typography>
                  </Box>
                  <Box
                    sx={{
                      transition: 'all 0.3s',
                      width: hoveredRow === index ? 32 : 0,
                      display: 'flex',
                      alignItems: 'center',
                    }}
                  >
                    <IconButton
                      onClick={(e) => {
                        e.stopPropagation()
                        deletePendingAction(row.token, row.tokenType)
                      }}
                      sx={{
                        transition: 'all 0.3s',
                        opacity: hoveredRow === index ? 1 : 0,
                        color: 'rgba(0, 0, 0, 1)',
                      }}
                    >
                      <ClearIcon sx={{ width: 16, height: 16 }} />
                    </IconButton>
                  </Box>
                </Box>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  )
}

const ShortTransactionPanel: React.FC = () => {
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null)
  const [maxSlippageValue, setMaxSlippageValue] = useState<any>(localStorage.getItem('slippage') ?? '0.5')
  const [deadline, setDeadlineValue] = useState<any>(10)
  const [approveFlag, setApproveFlag] = useState(false)

  const { chainId } = useWeb3React()
  const { mintSellHelpContract, signer, tokenPools } = useSystemParams()
  const { walletTokenBalance } = useHashPowerToken()
  const { toastError, toastSuccess, toastInfo } = useToast()

  const { pendingActionList, shortAmount } = useShortPendingAction()
  const { activeInfo, setActiveAmount, clearActive } = useShortActiveAction()
  const { onApprove, approveStatus, checkApproveLoading } = useShortActiveTokenApprove()

  const { addPendingAction, updatePendingAction, clearAllPendingAction, pendingTokenTypes } = useShortPendingAction()
  const { rateInfo, onChangeCollateralIndex } = useShortInfo()

  const { currentPosition, collateralIndex, currentMintToken } = usePositionInfo()
  const { newPrepaidInterestCoverage, newCollateralRatio, onUpdateCollateralValue, onUpdateNeutralizeValue } =
    useComputedIndexAction()
  const { handleUpdatePosition } = usePositionAction()

  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget)
  }
  const handleClose = () => {
    setAnchorEl(null)
  }

  const onMax = () => {
    if (activeInfo.tokenType !== 'Short') {
      setActiveAmount((walletTokenBalance as any)[activeInfo?.token ?? currentMintToken])
    }
  }

  const _tokenList = useCallback(() => {
    return pendingActionList
      .filter((item) => item.tokenType !== TokenType.SHORT && item.tokenType !== TokenType.NEUTRALIZE)
      .map((item) => chainConfigs[chainId as SupportedChainId].tokens[valueToKeyMap[item.token]].address)
  }, [chainId, pendingActionList])

  const _tokenAmounts = useCallback(() => {
    return pendingActionList
      .filter((item) => item.tokenType !== TokenType.SHORT && item.tokenType !== TokenType.NEUTRALIZE)
      .map((item) =>
        toSmallUnits(
          item.amount.toString(),
          chainConfigs[chainId as SupportedChainId].tokens[valueToKeyMap[item.token]].decimals,
        ),
      )
  }, [chainId, pendingActionList])

  // When the short value is not added, but collateral is added, update the collateral value.
  useEffect(() => {
    let amount = 0
    let _shortAmount = 0
    pendingActionList.forEach((action) => {
      if (action.tokenType === TokenType.SUPPLY) {
        if (currentPosition.mintTokenInfo.settlementCurrency.symbol === action.token) {
          const amountOut = action.amount
          amount += amountOut
        } else {
          const amountOut = getSwapQuote(
            chainId,
            action.token as unknown as SupportedTokens,
            currentPosition.mintTokenInfo.settlementCurrency.symbol,
            toSmallUnits(
              String(action.amount),
              chainConfigs[chainId as SupportedChainId].tokens[valueToKeyMap[action.token]].decimals,
            ),
            tokenPools,
          )
          amount += Number(toLargeUnits(amountOut, currentPosition.mintTokenInfo.settlementCurrency.decimals))
        }
      } else if (action.tokenType === TokenType.SHORT) {
        _shortAmount = Number(action.amount)
      }
    })
    onUpdateCollateralValue({
      symbol: currentPosition.mintTokenInfo.settlementCurrency.symbol as any,
      value: amount,
    })

    onUpdateNeutralizeValue(_shortAmount)
  }, [
    chainId,
    currentPosition.mintTokenInfo.settlementCurrency.decimals,
    currentPosition.mintTokenInfo.settlementCurrency.symbol,
    onUpdateCollateralValue,
    onUpdateNeutralizeValue,
    pendingActionList,
    tokenPools,
  ])

  const open = useMemo(() => Boolean(anchorEl), [anchorEl])
  const computedRate = useMemo(async () => {
    if (mintSellHelpContract && mintSellHelpContract.previewPositionInfoAfterShort) {
      if (!shortAmount) {
        onChangeCollateralIndex({
          ratio: Number(TokenUtils.formatTokenAmount(newCollateralRatio, 2)),
          coverage: Number(TokenUtils.formatTokenAmount(newPrepaidInterestCoverage, 0)),
        })
        return {
          targetCollateralRatio: collateralIndex.collateralRatio,
        }
      }
      const result = await mintSellHelpContract.previewPositionInfoAfterShort(
        currentPosition.positionAddress,
        toSmallUnits(shortAmount.toString(), currentPosition.mintTokenInfo.decimals),
        _tokenList(),
        _tokenAmounts(),
      )
      onChangeCollateralIndex({
        ratio: result.targetCollateralRatio.toNumber() / 100,
        coverage: Number(TokenUtils.formatTokenAmount(newPrepaidInterestCoverage, 0)),
      })
      return result
    }
    return { targetCollateralRatio: 0 }
  }, [
    mintSellHelpContract,
    shortAmount,
    currentPosition.positionAddress,
    currentPosition.mintTokenInfo.decimals,
    _tokenList,
    _tokenAmounts,
    onChangeCollateralIndex,
    newPrepaidInterestCoverage,
    newCollateralRatio,
    collateralIndex.collateralRatio,
  ])

  const submitShort = useCallback(async () => {
    const result = await computedRate

    const inter = new utils.Interface(contracts.actionMintSell.abi)
    const { decimals } = currentPosition.mintTokenInfo
    const data = inter.encodeFunctionData('short', [
      toSmallUnits(shortAmount.toString(), decimals),
      toSmallUnits(
        TokenUtils.formatTokenAmount(shortAmount - shortAmount * (maxSlippageValue / 100), decimals),
        decimals,
      ),
      _tokenList(),
      _tokenAmounts(),
      result.targetCollateralRatio,
      Number((Date.now() / 1000).toFixed()) + deadline * 60,
    ])
    try {
      const posContract = new ethers.Contract(currentPosition.positionAddress, PositionABI, signer)
      const txInfo = await posContract.delegateCall(contracts.actionMintSell.address[chainId], data)
      try {
        if (txInfo !== undefined && txInfo.hash) {
          toastInfo(
            ToastTitle.submited,
            <TransactionSubmittedInfo content="Waiting for confirmation" txHash={txInfo.hash} />,
          )
          const info = await txInfo.wait()

          if (info.confirmations !== 0) {
            toastSuccess(
              ToastTitle.success,
              <TransactionSubmittedInfo content="The transaction has been successfully" txHash={txInfo.hash} />,
            )
            clearActive()
            clearAllPendingAction()

            await handleUpdatePosition()
          }
        }
      } catch (error: any) {
        toastError(ToastTitle.failed, error.reason)
      }
    } catch (error: any) {
      toastError(ToastTitle.failed, error.reason)
    }
  }, [
    _tokenAmounts,
    _tokenList,
    chainId,
    clearActive,
    clearAllPendingAction,
    computedRate,
    currentPosition.mintTokenInfo,
    currentPosition.positionAddress,
    deadline,
    handleUpdatePosition,
    maxSlippageValue,
    shortAmount,
    signer,
    toastError,
    toastInfo,
    toastSuccess,
  ])

  const disabledAddAction = useMemo(() => {
    const _balance = walletTokenBalance && walletTokenBalance[activeInfo.token]
    const condition1 = activeInfo.amount && activeInfo.amount > _balance
    const condition2 = Number(activeInfo.amount) <= 0
    if (activeInfo.token) {
      const actionAmount =
        Number(activeInfo.amount) *
        10 ** chainConfigs[chainId as SupportedChainId].tokens[valueToKeyMap[activeInfo.token]].decimals
      if (activeInfo.amount && actionAmount > Number.MAX_SAFE_INTEGER) {
        return true
      }
    }

    return (activeInfo.tokenType !== TokenType.SHORT && condition1) || condition2
  }, [activeInfo.amount, activeInfo.token, activeInfo.tokenType, chainId, walletTokenBalance])

  const disabledSubmit = useMemo(() => {
    let { coverage } = rateInfo
    if (shortAmount) {
      if (rateInfo.rate < 120) {
        coverage = 'N/A'
      } else if (rateInfo.rate >= 120 && coverage < 20) {
        coverage = 20
      }
    }
    return !(rateInfo.rate >= 120 && coverage >= 20)
  }, [rateInfo, shortAmount])
  const isUpdate = useMemo(() => {
    return activeInfo.activeIndex !== -1
  }, [activeInfo])

  const [availableShowFlag, setAvailableShowFlag] = useState(false)

  const handleAction = useCallback(async () => {
    if (disabledAddAction) return
    const action = {
      token: activeInfo.token,
      tokenType: activeInfo.tokenType,
      amount: Number(activeInfo.amount),
      price: activeInfo.amount,
    }
    if (currentMintToken !== activeInfo.token) {
      const amountInDecimals =
        chainConfigs[chainId as SupportedChainId].tokens[valueToKeyMap[activeInfo.token]].decimals
      const amountIn = BigNumber.from(TokenUtils.formatTokenAmount(activeInfo.amount * 10 ** amountInDecimals, 0))
      const amountOut = getSwapQuote(
        chainId,
        activeInfo.token,
        currentMintToken as unknown as SupportedTokens,
        amountIn,
        tokenPools,
      )
      action.price = TokenUtils.formatFourDecimals(
        Number(toLargeUnits(amountOut, currentPosition.mintTokenInfo.decimals)),
      )
    }
    if (!approveStatus) {
      setApproveFlag(true)
      try {
        const tx = await onApprove()
        toastInfo(
          ToastTitle.submited,
          <TransactionSubmittedInfo content="The approve has been submitted" txHash={tx.hash} />,
        )
        const receipt = await tx.wait()
        if (receipt.status === 1) {
          updatePendingAction(action)
          clearActive()
          setApproveFlag(false)
          toastSuccess(
            ToastTitle.success,
            <TransactionSuccessInfo txHash={receipt.transactionHash} content="The approve has been successfully" />,
          )
        }
      } catch (err: any) {
        setApproveFlag(false)
      }
    } else if (isUpdate) {
      updatePendingAction(action)
      clearActive()
    } else {
      addPendingAction(action)
      clearActive()
    }
  }, [
    activeInfo.amount,
    activeInfo.token,
    activeInfo.tokenType,
    addPendingAction,
    approveStatus,
    chainId,
    clearActive,
    currentMintToken,
    currentPosition.mintTokenInfo.decimals,
    disabledAddAction,
    isUpdate,
    onApprove,
    toastInfo,
    toastSuccess,
    tokenPools,
    updatePendingAction,
  ])

  return (
    <Box sx={{ width: '100%' }}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <Typography sx={{ fontSize: 16, fontWeight: 500 }}>
          {activeInfo?.token ? `${activeInfo.tokenType} ${activeInfo.token}` : `${currentMintToken} Wallet Balance`}
        </Typography>

        <IconButton
          aria-controls={open ? 'basic-menu' : undefined}
          aria-haspopup="true"
          aria-expanded={open ? 'true' : undefined}
          onClick={handleClick}
          sx={{
            p: 0,
            width: 24,
            height: 24,
          }}
        >
          <SetIcon />
        </IconButton>

        <TradeSettings
          handleClose={handleClose}
          anchorEl={anchorEl}
          maxSlippageValue={maxSlippageValue}
          open={open}
          deadline={deadline}
          setMaxSlippageValue={(value) => {
            localStorage.setItem('slippage', value)
            setMaxSlippageValue(value)
          }}
          setDeadlineValue={(value) => setDeadlineValue(value)}
        />
      </Box>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mt: 1.6 }}>
        <Box
          sx={{
            display: 'flex',
            alignItems: 'center',
            gap: 1.2,
            width: '100%',
            height: '45px',
            transition: '300ms all',
          }}
        >
          {activeInfo?.token ? (
            <Logo token={commonTokens[valueToKeyMap[activeInfo.token]].logo} imgprops={{style:{width: 42, height: 42}}} symbol={activeInfo.token} />
          ) : (
            <Logo token={commonTokens[valueToKeyMap[currentMintToken]].logo} imgprops={{style:{width: 42, height: 42}}} symbol={currentMintToken} />
          )}
          <div
            style={{
              width: '100%',
              position: 'relative',
            }}
          >
            <InputBox>
              <input
                readOnly={!activeInfo?.token}
                type="number"
                placeholder={!activeInfo?.token ? walletTokenBalance[currentMintToken] : 0}
                value={
                  activeInfo?.amount.toString() ? getFullNum(activeInfo?.amount, commonTokens[activeInfo.token].decimals) : ''
                }
                onFocus={() => setAvailableShowFlag(true)}
                onBlur={() => {
                  if (activeInfo?.amount.toString() === '' || activeInfo?.amount < 0) {
                    setAvailableShowFlag(false)
                  }
                }}
                onChange={(event) => {
                  const inputValue = event.target.value
                  let formattedValue = ''
                  const cleanedValue = inputValue.replace(/[^\d.]/g, '')
                  if (cleanedValue.includes('.')) {
                    const parts = cleanedValue.split('.')
                    parts[1] = parts[1]?.slice(
                      0,
                      chainConfigs[chainId as SupportedChainId].tokens[valueToKeyMap[activeInfo.token]].decimals,
                    )
                    formattedValue = parts.join('.')
                  } else {
                    formattedValue = cleanedValue
                  }
                  // const isMaxVal = Number(formattedValue) * 10** currentPosition.mintTokenInfo.decimals > ;
                  // if (formattedValue *)
                  setActiveAmount(formattedValue || '')
                }}
              />
            </InputBox>

            {activeInfo?.token && availableShowFlag && activeInfo.tokenType !== 'Short' ? (
              <AvailableBox>
                <Box
                  sx={{
                    display: 'flex',
                    alignItems: 'center',
                    gap: 1,
                    width: '100%',
                    color: 'rgba(0,0,0,.5)',
                    paddingLeft: '4px',
                  }}
                >
                  <AvailableText>
                    {(walletTokenBalance as any)[activeInfo?.token ?? currentMintToken]} Available
                  </AvailableText>
                </Box>
              </AvailableBox>
            ) : (
              <></>
            )}
          </div>
        </Box>
        {activeInfo?.token && activeInfo.tokenType !== TokenType.SHORT && (
          <Box>
            <Button
              size="small"
              sx={{
                textTransform: 'none',
                borderRadius: 1.8,
                color: 'rgba(110, 214, 243, 1)',
              }}
              onClick={() => onMax()}
            >
              Max
            </Button>
          </Box>
        )}
      </Box>
      {activeInfo?.token && (
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            gap: 3,
            mt: 3,
          }}
        >
          <ActionButton
            variant="contained"
            disableElevation
            onClick={() => {
              clearActive()
            }}
          >
            Cancel
          </ActionButton>
          {checkApproveLoading ? (
            <ActionButton disabled variant="contained" disableElevation>
              Check Approving
            </ActionButton>
          ) : (
            <ActionButton
              disabled={disabledAddAction || approveFlag}
              variant="contained"
              disableElevation
              onClick={handleAction}
            >
              {!approveStatus ? 'Approve & Add' : `${isUpdate ? 'Update' : 'Add'} Action`}
            </ActionButton>
          )}
        </Box>
      )}
      {pendingActionList.length > 0 && (
        <Box>
          <ActionList />
        </Box>
      )}
      {pendingTokenTypes.get(currentMintToken as unknown as string)?.includes(TokenType.SHORT) && (
        <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', mt: 3 }}>
          <ActionButton onClick={() => submitShort()} variant="contained" disableElevation disabled={disabledSubmit}>
            Submit Transaction
          </ActionButton>
        </Box>
      )}

      <ShortEstimatedDashBoard />
    </Box>
  )
}
export default ShortTransactionPanel
