import { Box, Typography } from '@mui/material'
import React, { useCallback, useEffect, useMemo } from 'react'
import SubdirectoryArrowRightIcon from '@mui/icons-material/SubdirectoryArrowRight'
import { useWeb3React } from 'web3-react-core'
import useSystemParams from 'hooks/useSystemParams'
import { TokenType } from 'state/Short/shortSlice'
import { useShortActiveAction, useShortPendingAction, useShortInfo } from 'state/Short/hooks'
import { TokenUtils, toLargeUnits } from 'utils/transformHelper'
import { useComputedIndexAction, usePositionInfo } from 'hooks/news/usePoistion'
import { DataIndexName } from 'config/constants/contants'
import { ShortThresholdValue } from 'config/constants/thresholdValue'

const ShortEstimatedDashBoard: React.FC = () => {
  const { chainId } = useWeb3React()
  const { tokenPools } = useSystemParams()

  const { supplyAllAmount, shortAmount } = useShortPendingAction()
  const { activeInfo } = useShortActiveAction()
  const { rateInfo } = useShortInfo()

  const { collateralIndex, currentPosition } = usePositionInfo()
  const {
    onUpdateNeutralizeValue,
    onUpdateCollateralValue,
    newOutStandingDebt,
    newEstimatedDailyDebt,
    newDisplayCollateralRatio,
    newPrepaidInterestCoverage,
  } = useComputedIndexAction()
  const displayDatas = useMemo(() => {
    const { decimals, settlementCurrency } = currentPosition.mintTokenInfo
    const current = TokenUtils.formatFourDecimals(Number(toLargeUnits(currentPosition.balance, decimals)))
    const _new = TokenUtils.formatFourDecimals(Number(toLargeUnits(newOutStandingDebt, decimals)))
    return {
      current,
      next: _new,
      newDisplayCollateralRatio,
      newEstimatedDailyDebt: TokenUtils.formatTokenAmount(
        newEstimatedDailyDebt / 10 ** settlementCurrency.decimals,
        settlementCurrency.decimals,
      ),
    }
  }, [
    currentPosition.balance,
    currentPosition.mintTokenInfo,
    newDisplayCollateralRatio,
    newEstimatedDailyDebt,
    newOutStandingDebt,
  ])
  const prepaidInterestNewVal = useMemo(() => {
    if (rateInfo.rate >= 120 && rateInfo.coverage < 20) {
      const minPI = TokenUtils.formatTokenAmount(
        Number(displayDatas.newEstimatedDailyDebt) * ShortThresholdValue.days,
        currentPosition.mintTokenInfo.settlementCurrency.decimals,
      )
      return minPI
    }
    return newPrepaidInterestCoverage
  }, [
    currentPosition.mintTokenInfo.settlementCurrency.decimals,
    displayDatas.newEstimatedDailyDebt,
    newPrepaidInterestCoverage,
    rateInfo.coverage,
    rateInfo.rate,
  ])
  const displayIndex = useMemo(() => {
    let { coverage, rate } = rateInfo
    if (shortAmount) {
      if (rateInfo.rate < 120) {
        coverage = 0
      } else if (rateInfo.rate >= 120 && coverage < 20) {
        coverage = 20
      } else if (coverage === Number.POSITIVE_INFINITY || coverage === 'Infinity') {
        coverage = Number.POSITIVE_INFINITY
      }
    } else if (coverage === Number.POSITIVE_INFINITY || coverage === 'Infinity') {
      coverage = Number.POSITIVE_INFINITY
    } else {
      coverage = collateralIndex.prepaidInterestCoverage
    }
    if (rateInfo.rate) {
      if (rateInfo.rate === Number.POSITIVE_INFINITY || rateInfo.rate === 'Infinity') {
        rate = Number.POSITIVE_INFINITY
      }
    } else {
      rate = collateralIndex.collateralRatio
    }

    return {
      rate,
      coverage,
    }
  }, [collateralIndex.collateralRatio, collateralIndex.prepaidInterestCoverage, rateInfo, shortAmount])

  const walletData = useMemo(() => {
    let { coverage, rate } = rateInfo
    if (shortAmount) {
      if (rateInfo.rate < 120) {
        coverage = 'N/A'
      } else if (rateInfo.rate >= 120 && coverage < 20) {
        coverage = `${20} days`
      } else if (coverage === Number.POSITIVE_INFINITY || coverage === 'Infinity') {
        coverage = '+∞'
      } else {
        coverage = `${coverage} days`
      }
    } else if (coverage === Number.POSITIVE_INFINITY || coverage === 'Infinity') {
      coverage = '+∞'
    } else {
      coverage = `${coverage} days`
    }
    if (rateInfo.rate) {
      if (rateInfo.rate === Number.POSITIVE_INFINITY || rateInfo.rate === 'Infinity') {
        rate = '+∞'
      } else {
        rate = `${rateInfo.rate}%`
      }
    } else {
      rate = collateralIndex.displayRatio
    }

    return [
      {
        id: 1,
        title: DataIndexName.CollateralRatio,
        type: 'ratio',
        value: {
          upVal: collateralIndex.displayRatio,
          downVal: rate,
        },
      },
      {
        id: 2,
        title: DataIndexName.PrepaidInterestCoverage,
        type: 'coverage',
        value: {
          upVal: collateralIndex.displayCoverage,
          downVal: coverage,
        },
      },
      {
        id: 3,
        title: DataIndexName.PrepaidInterest,
        type: 'prepaidInterest',
        value: {
          upVal: `${TokenUtils.formatFourDecimals(collateralIndex.prepaidInterest)} ${currentPosition.mintTokenInfo.settlementCurrency.symbol}`,
          downVal: `${TokenUtils.formatFourDecimals(Number(prepaidInterestNewVal))} ${currentPosition.mintTokenInfo.settlementCurrency.symbol}`,
        },
      },
      {
        id: 4,
        title: 'Daily Paid Interest Reference',
        type: 'dailyPaid',
        value: {
          upVal: `${TokenUtils.formatFourDecimals(Number(toLargeUnits(
            currentPosition.positionStatus.estimatedDailyDebt,
            currentPosition.mintTokenInfo.settlementCurrency.decimals,
          )))} ${currentPosition.mintTokenInfo.settlementCurrency.symbol}`,
          downVal: `≈ ${TokenUtils.formatFourDecimals(Number(displayDatas.newEstimatedDailyDebt))} ${currentPosition.mintTokenInfo.settlementCurrency.symbol}`,
        },
      },
      {
        id: 5,
        title: 'Outstanding Debt',
        type: 'debt',
        value: {
          upVal: displayDatas.current,
          downVal: `≈ ${displayDatas.next}`,
        },
      },
    ]
  }, [
    rateInfo,
    shortAmount,
    collateralIndex.displayRatio,
    collateralIndex.displayCoverage,
    collateralIndex.prepaidInterest,
    currentPosition.mintTokenInfo.settlementCurrency.symbol,
    currentPosition.mintTokenInfo.settlementCurrency.decimals,
    currentPosition.positionStatus.estimatedDailyDebt,
    prepaidInterestNewVal,
    displayDatas,
  ])

  const colorValue = useCallback(
    (item: any) => {
      const redss =
        (item.type === 'ratio' && displayIndex.rate < ShortThresholdValue.ratio) ||
        (item.type === 'coverage' && displayIndex.coverage < ShortThresholdValue.days)
          ? '#FF4D00'
          : '#2AA1FF'
      return ['prepaidInterest', 'dailyPaid', 'debt'].includes(item.type) ? '#2AA1FF' : redss
    },
    [displayIndex.coverage, displayIndex.rate],
  )

  useEffect(() => {
    let amount = 0
    if (activeInfo.token && activeInfo.tokenType === TokenType.SHORT) {
      amount = Number(activeInfo.amount)
    } else if (shortAmount) {
      amount = Number(shortAmount)
    }

    onUpdateNeutralizeValue(amount)
  }, [activeInfo, onUpdateNeutralizeValue, shortAmount])

  return (
    <Box
      sx={{
        bgcolor: 'rgba(222, 241, 255, 1)',
        borderRadius: 1.8,
        mt: 4,
        p: 2,
        display: 'flex',
        flexDirection: 'column',
        gap: 1.2,
      }}
    >
      {walletData.map((item: any) => {
        return (
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }} key={item.id}>
            <Typography sx={{ fontSize: 12, color: 'rgba(0,0,0,0.5)' }}>{item.title}</Typography>

            <Box>
              <Typography sx={{ fontSize: 12, color: 'rgba(0,0,0,0.6)', textAlign: 'right' }}>
                {item.value.upVal}
              </Typography>
              {((shortAmount || supplyAllAmount) && (
                <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'end', textAlign: 'right' }}>
                  <SubdirectoryArrowRightIcon sx={{ color: 'rgba(0,0,0,0.1)', width: 12, height: 12 }} />
                  <Typography
                    sx={{
                      fontSize: 10,
                      color: colorValue(item),
                    }}
                  >
                    {item.value.downVal}
                  </Typography>
                </Box>
              )) ||
                null}
            </Box>
          </Box>
        )
      })}
    </Box>
  )
}
export default ShortEstimatedDashBoard
